import React, { Suspense, useState, useEffect } from 'react';
import { renderRoutes } from 'react-router-config';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import { LinearProgress } from '@material-ui/core';

import { NavBar, TopBar } from './components';
import { initSocket } from './../../utils/socket.io'
import { connect } from 'react-redux';
import { listNotifications } from '../../api/api'
import { onAddNotifications } from '../../actions'


const useStyles = makeStyles(() => ({
  root: {
    height: '100%',
    width: '100%',
    display: 'flex',
    flexDirection: 'column',
    overflow: 'hidden'
  },
  topBar: {
    zIndex: 2,
    position: 'relative'
  },
  container: {
    display: 'flex',
    flex: '1 1 auto',
    overflow: 'hidden'
  },
  navBar: {
    zIndex: 3,
    width: 256,
    minWidth: 256,
    flex: '0 0 auto'
  },
  content: {
    overflowY: 'auto',
    flex: '1 1 auto'
  }
}));

const Dashboard = props => {
  const { route } = props;

  const classes = useStyles();
  const [openNavBarMobile, setOpenNavBarMobile] = useState(false);
  const [socket, setSocket] = useState(null);

  const handleNavBarMobileOpen = () => {
    setOpenNavBarMobile(true);
  };
  const handleNavBarMobileClose = () => {
    setOpenNavBarMobile(false);
  };

  useEffect(() => {
    const socket = initSocket();
    socket.on('onCreateRequest', () => {
      console.log("::::::::::::::::::::::::::::::::::::::::::::::::");
      listNotifications({}).then(response => {
        console.log(response)
        props.addNotifications(response.notifications);
        // setNotifications(response.notifications);
      });
    });
  }, []);
  return (
    <div className={classes.root}>
      <TopBar
        socket={socket}
        className={classes.topBar}
        onOpenNavBarMobile={handleNavBarMobileOpen}
      />
      <div className={classes.container}>
        <NavBar
          className={classes.navBar}
          onMobileClose={handleNavBarMobileClose}
          openMobile={openNavBarMobile}
        />
        <main className={classes.content}>
          <Suspense fallback={<LinearProgress />}>
            {renderRoutes(route.routes)}
          </Suspense>
        </main>
      </div>
    </div>
  );
};

Dashboard.propTypes = {
  route: PropTypes.object
};

const mapStateToProps = state => {
  return {
    notifications: state.notifications
  }
}
const mapDispatchToProps = dispatch => {
  return {
    addNotifications(notifications) {
      dispatch(onAddNotifications(notifications))
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);