import io from 'socket.io-client'
import config from './../config'

var socket = null

const initSocket = () => {

    if (localStorage.getItem('token') !== null) {
        socket = io.connect(`${config.socketServer}`, {
            transports: ['websocket'],
            upgrade: true,
            path: config.pathSocket,
            query: {
                token: localStorage.getItem('token')
            }
        })
        socket.on('reconnect_attempt', () => {
            socket.io.opts.transports = ['polling', 'websocket'];
        });
    }
    return socket;

}

const acceptRequest = request => {
    console.log("#########################################################");
    console.log(request);
    console.log(socket);
    console.log("#########################################################");

    if (socket !== null) {
        socket.emit('acceptRequest', { request })
    }
}
const updateMenu = data => {
    console.log("#########################################################");
    console.log(data);
    console.log("#########################################################");

    if (socket !== null) {
        socket.emit('updateMenu', data)
    }
}

export {
    initSocket,
    acceptRequest,
    updateMenu,
    socket
}